import { produce } from 'immer'
import { isFunction, isString, isObject } from 'lodash'
import { defineMessages as baseDefineMessages } from 'react-intl'

export const isValidString = sth => isString(sth) && sth.length > 0

export const createReducer = (initialState = {}, handlers = {}) =>
  (state = initialState, action) =>
    produce(state, draft => {
      if (isFunction(handlers[action.type])) {
        return handlers[action.type](draft, action)
      }
    })

export const defineMessages = (scope, messageObj) => {
  if (!isObject(messageObj)) {
    return messageObj
  }

  const messages = baseDefineMessages(messageObj)

  let finalScope = scope
  if (!isValidString(finalScope)) {
    finalScope = 'valonekowd.app'
  }

  const proxy = new Proxy(messages, {
    get: (target, prop) => ({
      id: `${finalScope}.${prop}`,
      defaultMessage: isString(target[prop]) ? target[prop] : '🔥',
    }),
  })

  return proxy
}
