import { createSelector } from 'reselect'

import { SUPPORTED_THEMES, DEFAULT_THEME, SUPPORTED_LOCALES, DEFAULT_LOCALE } from '../../utils/constants'
import { createReducer } from '../../utils/helpers'

const initialState = {
  theme: DEFAULT_THEME,
  locale: DEFAULT_LOCALE,
}

export const types = new Proxy({
  SET_APP_THEME: 'SET_APP_THEME',
  SET_APP_LOCALE: 'SET_APP_LOCALE',
}, {
  get: (target, prop) => target[prop] ? `valonekowd/app/${target[prop]}` : target[prop],
})

export const actions = {
  setAppTheme: theme => ({ type: types.SET_APP_THEME, theme }),
  setAppLocale: locale => ({ type: types.SET_APP_LOCALE, locale }),
}

export const reducer = createReducer(initialState, {
  [types.SET_APP_THEME]: (draft, { theme }) => {
    if (SUPPORTED_THEMES.includes(theme)) {
      draft.theme = theme
    }
  },
  [types.SET_APP_LOCALE]: (draft, { locale }) => {
    if (SUPPORTED_LOCALES.includes(locale)) {
      draft.locale = locale
    }
  },
})

export const selectors = {
  selectApp: state => state.app || initialState,
  makeSelectTheme() {
    return createSelector(this.selectApp, ({ theme }) => theme || DEFAULT_THEME)
  },
  makeSelectLocale() {
    return createSelector(this.selectApp, ({ locale }) => locale || DEFAULT_LOCALE)
  },
}
