import { lazy as baseLazy } from 'react'

const lazy = importedPromise => baseLazy(
  () => Promise.all([
    importedPromise,
    new Promise(resolve => setTimeout(resolve, 300)),
  ]).then(([importedModule]) => importedModule),
)

const HomePage = lazy(import(/* webpackChunkName: "home" */ './pages/Home'))
const AboutPage = lazy(import(/* webpackChunkName: "about" */ './pages/About'))
const WorkPage = lazy(import(/* webpackChunkName: "work" */ './pages/Work'))
const BlogPage = lazy(import(/* webpackChunkName: "blog" */ './pages/Blog'))
const ContactPage = lazy(import(/* webpackChunkName: "contact" */ './pages/Contact'))
// const NotFoundPage = lazy(import(/* webpackChunkName: "not-found" */ './containers/NotFoundPage'))

const routes = [
  { path: '/', name: 'Home', component: HomePage, exact: true },
  { path: '/about', name: 'About', component: AboutPage },
  { path: '/work', name: 'Work', component: WorkPage },
  { path: '/blog', name: 'Blog', component: BlogPage },
  { path: '/contact', name: 'Contact', component: ContactPage },
//   { name: 'NotFound', component: NotFoundPage },
]

export default routes
